@import "../../Styles/Variables.scss";

.footer {
  background-image: url("../../../public/assets/footer.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
  padding: 80px 0;
  border-top: 4px solid $gold__color;
  // min-height: 70vh;
  margin-top: 100px;
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000be;
  }
  .container {
    position: relative;
    z-index: 20;
    .footer_content {
      display: flex;
      align-items: center;
      justify-content: space-between;
      color: $color__white;
      gap: 40px;
      .left,
      .center,
      .rigth {
        flex: 1;
      }
      .center {
        flex: 1.4;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        row-gap: 24px;
        img {
          width: 100%;
          max-width: 500px;
        }
        ul {
          display: flex;
          align-items: center;
          justify-content: space-between;
          font-size: 28px;
          gap: 28px;
          li {
            a {
              color: $gold__color;
              background-color: transparent;
              background-color: #0000002c;
              backdrop-filter: blur(10px);
              width: 50px;
              height: 50px;
              display: flex;
              align-items: center;
              justify-content: center;
              border-radius: 50%;
              border: 2px solid $gold__color;
              transition: 0.3s ease-in-out;
              &:hover {
                color: $black__color;
                background-color: $gold__color;
                border-color: $black__color;
              }
            }
          }
        }
      }
      .left,
      .rigth {
        display: flex;
        flex-direction: column;
        .footer__title {
          color: $gold__color;
          display: flex;
          align-items: center;
          gap: 4px;
          font-size: $ft__24;
          margin-bottom: 24px;
        }
        .footer__item {
          position: relative;
          margin: 0 0 12px 24px;
          font-size: 18px;
          &::after {
            content: "";
            position: absolute;
            top: 50%;
            left: -10px;
            transform: translate(-100%, -50%);
            width: 10px;
            height: 4px;
            background-color: $color__white;
          }
        }
      }
      .rigth {
        .footer__item {
          line-height: 28px;
          &::after {
            top: 14px;
            transform: translate(-100%, 0);
          }
        }
        .location__btn {
          background-color: transparent;
          backdrop-filter: blur(10px);
          color: $gold__color;
          border: 2px solid $gold__color;
          width: fit-content;
          padding: 12px 40px;
          font-size: $ft__18;
          font-weight: $ft__W__M;
          border-radius: 50px;
          transition: 0.3s ease-in-out;
          &:hover {
            color: $black__color;
            background-color: $gold__color;
            border-color: $black__color;
          }
        }
      }
      .left {
        flex: 0.6;
      }
    }
  }
}
